$sans-font-stack: Verdana, Arial, Helvetica, sans-serif;
$full-screen-sidenav-allotment: 200px;
$mobile-screen-sidenav-allotment: 40px;
$cornflower: #4459e2;
$pale-cornflower: #8bb1ef;
$deep-cornflower: #224d93;
$light-gray: #ccc;
$smallscreen: 768px;

* { box-sizing: border-box; }
html { background: white; }
body { 
  font-family: $sans-font-stack; 
  // Body has a default margin. Override.
  margin: 0px; 

  // Clicks via 'onclick' make the screen flash on Safari mobile without this.
  -webkit-tap-highlight-color: transparent;
}

.sidenav {
  height: 100%;
  width: $full-screen-sidenav-allotment;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  display: block;
  background-color: $light-gray;
  padding-left: 0px;
  box-sizing: border-box; 
  transition: 0.3s;

  a {
    padding: 4px 8px 4px 16px;
    font-size: 140%;
    text-decoration: none;
    display: block;
    color: $cornflower;
  }

  a:hover {
      background-color: $pale-cornflower;
  }

  h1 {
    font-size: 105%;

    // h1 has a default top/bottom margin that needs overruling
    // margin: 0px;

    a {
      color: black;
    }

    a:hover {
        background-color: $light-gray;
    }
  }
}


.sidenavburger {
    font-size:30px!important;
    width: $mobile-screen-sidenav-allotment;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    padding-left: 5px;
    overflow-x: hidden;
    display: block;
}


@media (max-width: $smallscreen - 1px){
  .sidenav{ width: 0; }
  .sidenav.showing{ width: $full-screen-sidenav-allotment + 10px;}
  .main-body {margin-left: $mobile-screen-sidenav-allotment }
}

@media (min-width:$smallscreen){
  .hide-large{display:none!important}
  .sidenav{ width: $full-screen-sidenav-allotment; }
  .main-body {margin-left: $full-screen-sidenav-allotment + 20px;}
}


.main-body {
   // Clicks via 'onclick' make the screen flash on Safari mobile without this.
  -webkit-tap-highlight-color: transparent;

  a {
    color: $cornflower;
    text-decoration: none;
  }

  a:visited {
    color: $deep-cornflower;  
  }  
}

table.asst-table {
    border: 2px solid black;
    border-collapse: separate;
    border-spacing: 20px 10px;
}

table.inner {
  td {
    padding-left: 10px
  }
}

dt {
  float: left;
  width: 30px;
  text-align: right;
  font-weight: bold;
}

dd {
  margin: 0 0 0 60px;
  padding: 0 0 0.5em 0;
}
